import React, { useState, useEffect } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";

export default function App() {
  const [notes, setNotes] = useState(() => {
    const savedNotes = localStorage.getItem("notes");
    try {
      if (savedNotes) {
        const parsedNotes = JSON.parse(savedNotes);
        return Array.isArray(parsedNotes) ? parsedNotes : [];
      } else {
        return [];
      }
    } catch (error) {
      console.log("Error parsing notes from local storage:", error);
      return [];
    }
  });

  const addNote = (newNote) => {
    setNotes((prevNotes) => {
      return [...prevNotes, newNote];
    });
  };

  // Using the useEffect hook to save the notes to local storage whenever the notes state changes
  useEffect(() => {
    localStorage.setItem("notes", JSON.stringify(notes));
  }, [notes]);

  const deleteNote = (indexToDelete) => {
    setNotes(notes.filter((note, index) => index !== indexToDelete));
  };

  return (
    <div>
      <Header />
      <CreateArea onAdd={addNote} />
      {notes.map((note, index) => {
        return (
          <Note
            key={index}
            id={index}
            title={note.title}
            content={note.content}
            onDelete={deleteNote}
          />
        );
      })}
      <Footer />
    </div>
  );
}
